module.exports = ".paging {\n  text-align: center;\n}\n\np {\n  font-family: Lato;\n}\n\n/* The container */\n\n.container {\n  display: block;\n  position: relative;\n  padding-left: 35px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-size: 22px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n/* Hide the browser's default checkbox */\n\n.container input {\n  position: absolute;\n  opacity: 0;\n  cursor: pointer;\n}\n\n/* Create a custom checkbox */\n\n.checkmark {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 25px;\n  width: 25px;\n  background-color: #eee;\n}\n\n/* On mouse-over, add a grey background color */\n\n.container:hover input ~ .checkmark {\n  background-color: #ccc;\n}\n\n/* When the checkbox is checked, add a blue background */\n\n.container input:checked ~ .checkmark {\n  background-color: #2196f3;\n}\n\n/* Create the checkmark/indicator (hidden when not checked) */\n\n.checkmark:after {\n  content: \"\";\n  position: absolute;\n  display: none;\n}\n\n/* Show the checkmark when checked */\n\n.container input:checked ~ .checkmark:after {\n  display: block;\n}\n\n/* Style the checkmark/indicator */\n\n.container .checkmark:after {\n  left: 9px;\n  top: 5px;\n  width: 5px;\n  height: 10px;\n  border: solid white;\n  border-width: 0 3px 3px 0;\n  -webkit-transform: rotate(45deg);\n  transform: rotate(45deg);\n}\n\n.room {\n  width: 80px;\n  height: 100px;\n  float: left;\n  margin-left: 10px;\n  margin-top: 10px;\n}\n\n.fa-male {\n  margin-top: 7px;\n  margin-right: 2px;\n  cursor: pointer;\n}\n\n.fa-female {\n  margin-top: 7px;\n  margin-right: 2px;\n  cursor: pointer;\n}\n\n.room-name {\n  background: #2196f3;\n  cursor: pointer;\n}\n\n.ps {\n  width: 20px;\n  height: 20px;\n}\n\n.bg-empty {\n  background: orange;\n}\n\n.bg-full {\n  background: #a3b869;\n}\n\n.bg-normal {\n  background: #9b9494;\n}\n/*# sourceMappingURL=data:application/json;base64, */"